@import '../../abstracts/mixins';

$stkCard_level_glow_color : lightblue;
$stkCard_tilt : 5deg;
$stkCard_spacing :-148px;
@include respond(big-desktop) { // width < 1200?
  top:20%;
  @include respond(tab-land) { // width < 1200?
  top:19%;
      @include respond(tab-port) { // width < 900px
          top:14%;
          @include respond(phone) { // width < 600px
    
}}}}

.stackstrip{
  background-color: rgb(248, 248, 248);
  display: flex;
  align-items: center;
  transform-style: preserve-3d;
  margin: 6rem 0;
  // perspective: 300px;

  &_container {
    display: flex;
    perspective: 500px;
    // flex-wrap: wrap;
    width: 100%;
    justify-content: center;
  }
}
.stackCard{
  
  background-size: cover;
  transform: rotateY($stkCard_tilt) ;//translateZ(12px);
  display: flex;
  height: 300px;
  width: 200px;
  border-radius: 10px;
  box-shadow: //-.1rem 0 4rem 1rem rgba(71, 71, 71, 0.1),
                      -7px 0 11px -2px  rgba(73, 73, 73,.4);;
                      
  transition:  0.4s  ease-out;
  position: relative;
  // border: 3px solid rgb(66, 66, 66);1
  transform-origin: left;
  overflow: hidden;

&_Conimg{
  height:100%;
  width: 100%;
  position:absolute;
  // border: 1px dashed rgb(240, 113, 181);
  border-radius: inherit;
}
//   // position: relative;
    &_Conimg_img{
      height:  120%;
      z-index: -1;
      object-fit: cover ;

      transition: transform .2s ease;
      transform: translate3d(-5%,-5%,0);
    position: absolute;
  }
  // display: none;
// }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 200%;
    background: linear-gradient(
      to bottom,
      
      rgba(11, 134, 206, 0.849),
      rgba(0, 74, 109, 0.24),
    
      rgba(99, 99, 99, 0.41),
      rgba(78, 78, 78, 0.57)
    );
    transition: transform 0.4s;
    // z-index: -1;
  }
  &:hover ~ & {  /*** Push(right) all the cards to the right ***/
    position: relative;

    transform: translateX(84%) rotateY($stkCard_tilt) ;// translateZ(12px) ;
    transition: 0.4s ease-out;
  }

  &:hover::before {
    transform: translateY(-50%);
  }
  

  &:not(:first-child) {
      margin-left: $stkCard_spacing;
  }

  &:hover {
    transform: translateY(0) ;// translateZ(22px); ///////////// causing a problem
    // background-position:-10px -50px;
    background-color: black;
    transition:  0.4s  linear , background-position 10s linear;
  }


  &:hover &_Conimg_img{
      
    transition: transform 77s linear;
    transform: translate3d(-33%,-33%,0);
  }


  &_title {
    padding:.5rem 0 ;
  transform: rotate(-.5turn) translateZ(87px);
    display: flex;
    flex-flow: row;
    justify-content: space-between;
    font-size: 1.4rem;

    writing-mode:vertical-rl;
    color: rgb(235, 235, 235);
    font-weight: 400;
     letter-spacing: .1ch;
    margin:1rem .4rem;
    position: relative;
    transition: all 1s ease-in-out ;
    &::after{
      // background-color: lightcoral;
      color:rgb(209, 209, 209);

      letter-spacing: .1ch;
      content: attr(subtitle);
    }
  }


 
  &_brief{
    font-family: 'Roboto', sans-serif;    
    // text-align: center;
    text-justify:inter-word;
    height: 100%;
    margin: 0 .8rem 0 .8rem;
    display: grid;
    align-items: center;
    font-size: 1.1rem;
    color: rgb(255, 255, 255);
    line-height: 19px;
    letter-spacing: .5px;
    font-weight: 300;

    padding-right: .1rem;
    opacity: 0;
    transition: .4s linear;
    position: relative;
    // transform: translateZ(83px);
    transition: opacity .4s;


  }
  &:hover &_brief{
    opacity: 1;
    line-height: 19px;
    // transform: translateZ(83px);
    transition: opacity 1s linear, transform 7s linear ;
  }



}
.stdcrdHide{
  position: absolute;
  // font-size: .6rem;
  p{
    font-size: .6rem;
  }

}



